.mo-br{display: none;}

.main{
  .header{
    > .logo{
      display:block;
      width:252px;
      height:24px;
      background:url($main_logo) 50% 50% no-repeat;
    }
    .btn--white{
      border-color:#fff;
      background-color:transparent;
      color:#fff;
    }
    .btn--black{
      border-color:#fff;
      background-color:#fff;
      color:#111;
    }
    .btn-menu span{
      background-color:#111;
    }
    .btn-menu{
      &.is-active{
        span{
          background-color:#111;
        }
      }
    }
    &.white{
      >.logo{
        background-image:url($white_logo);
      }
      .util{
        button{
          &.btn{
            &-login{
              background-color: #fff;
              span{
                color: #000;
              }
              &.user{
                background-color: transparent;
                
                &::before{
                  background: url($arrow_s_ico_white) 50% no-repeat;
                }
                span{
                  color: #fff;
                }
              }
            }
            &-test{
              span{
                color: #fff;
              }
            }
            &-mall{
              span{
                color: #fff;
              }
            }
          }
        }
      }
      .is-active{
        .btn-menu span{
          background-color:#111;
        }  
      }
      .btn-menu span{
        background-color:#fff;
      }
    }
  }
  .container{
    min-height: 100vh;
    margin-top: 0;
  }
  .section{
    overflow:hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    //padding: 0;
    height: 100vh;
    padding-top: 9.6rem;

    &:first-child{
      padding-top: 9.6rem;
    }
    .main-contents{
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      //justify-content: space-between;
      width: 100%;
      height: calc(100vh - 9.6rem);
    }
  }
  &-text{
    h2{
      font-family: 'binggrae';
      font-size: 3.2rem; //2rem
      font-weight: 700;
      letter-spacing: -.045rem;
      line-height: 1.4;
      @include smoothEffect;
      
      strong{
        display: block;
        margin-top: 2.4rem;
        font-family: 'binggrae';
        font-size: 6.4rem;  //3.5rem
        font-weight: 700;
        letter-spacing: -.08rem;
        line-height: 1.4;
        @include smoothEffect;
        
        span{
          position: relative;
          font-size: inherit;
          font-family: 'binggrae';
          font-weight: 700;
        }
        span::before{
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          z-index: -1;
          width: 97px;
          height: 88px;
          background: url($round_line) no-repeat;
          background-size: contain;
        }
      }
    }
    p{
      margin-top: 4rem;
      font-family: 'pretendard';
      font-size: 2.2rem;
      letter-spacing: -0.028rem;
      line-height: 1.4;
      word-break: keep-all;
    }
    .btn-wrap{
      margin-top: 8rem;
      text-align: left;
    }
  }
  #section01{
    width: 100%;  
    background: url('../../resources/img/web/main/book_v1_bg.png') no-repeat;
    background-size: 100% 100%;

    .main-contents{
      padding: 0 10rem;
    }
    .main-text{
      position: absolute;
      left: 8vw;

      h2{
        color: #09A0FF;
        font-size: 3.6rem;
        
        strong{
          color: #111;
          font-size: 6.4rem;
        }
      }
      p{
        color: #666;
      }
      .btn-wrap{
        .btn{
          display: inline-block;
          padding: 2.2rem 4rem;
        }
      }
    }
    .main-visual{
      &-wrap{
        position: relative;
        width: 100%;
        height: 100%;

        .only-mob{
          display: none;
        }
        .only-pc{
          position: relative;
          width: 100%;
          height: 100%;
          z-index: 0;
          margin-left: auto;

          .img-area{
            width: 46.6vw;
            position: absolute;
            right: 0;
            bottom: 0;
            z-index: 1;
            @include smoothEffect;

            .imgs{
              position: relative;
              width: 90%;
              z-index: 1;
            }
          }
          .main-visual-item{
            &.n1{
              left: 0.1vw;
              top: 0.2vw;
              // width: 17.188rem;
              // height: 20.25rem;
              width: 14.32vw;
              height: 16.875vw;
              background: url('../../resources/img/web/main/book_v1_bg01.png') no-repeat;
              background-size: 100% auto;
            }
            &.n2{
              right: 18.69vw;
              top: -5vw;
              width: 4.53vw;
              height: 6.4vw;
              background: url('../../resources/img/web/main/book_v1_bg02.png') no-repeat;
              background-size: 100% auto;
            }
            &.n3{
              // width: 463px;
              // height: 191px;
              right: 10.57vw;
              top: -7vw;
              width: 24.11vw;
              height: 9.94vw;
              background: url('../../resources/img/web/main/book_v1_bg03.png') no-repeat;
              background-size: contain;
            }
            &.n4{
              // width: 104px;
              // height: 86px;
              right: -1vw;
              top: .98vw;
              width: 5.41vw;
              height: 4.47vw;
              background: url('../../resources/img/web/main/book_v1_bg04.png') no-repeat;
              background-size: contain;
            }
            &.n5{
              right: -.3vw;
              top: 10.8vw;
              width: 10.1vw;
              height: 6.9vw;
              background: url('../../resources/img/web/main/book_v1_bg05.png') no-repeat;
              background-size: contain;
            }
            &.n6{
              left: -25vw;
              top: -13vw;
              width: 17.96vw;
              height: 17.96vw;
              background: url('../../resources/img/web/main/book_v1_bg06.png') no-repeat;
              background-size: contain;
            }
          }  
        }
      }
      &-item{
        position: absolute;
      }
    }
  }
  #section02{
    background-color:#09A0FF;

    &::before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      width: 100%;
      height: 100%;
      background: url('../../resources/img/web/main/book_v2_pattern01.png') no-repeat;
      background-size: 100% auto;
    }
    &::after{
      content: '';
      position: absolute;
      left: 0;
      bottom: -7.29vw;
      display: block;
      width: 100%;
      height: 36.19vw;
      background: url('../../resources/img/web/main/book_v2_pattern02.png') no-repeat;
      background-size: 100% auto;
    }

    .main-text{
      h2{
        color: #fff;      
        font-size: 3.2rem;
        
        strong{
          color: #fff;
          font-size: 5.6rem;
        }
      }
      p{
        margin-top: 4.6rem;
        color: #fff;
      }
      .stxt{
        margin-top: 6.2rem;
        font-size: 1.4rem;
        font-weight: 400;
      }
    }
    .main-visual{
      position: relative;
      width: 60rem;
      height: 100%;
      display: flex;
      align-items: center;
      margin-right: 7rem ;

      &-item{
        z-index: 1;
        width: 60.2rem;
        height: 50.8rem;
        background: url('../../resources/img/web/main/book_v2_img01.png') no-repeat 50%;
        background-size: contain;
      }
    }
  }
  #section03{
    &::before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      z-index: -1;
      display: block;
      width: 35.83%;
      height: 100%;
      background: url('../../resources/img/web/main/book_v3_pattern01.png') no-repeat 100% 0;
      background-size: 100%;
    }
    &::after{
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      z-index: -1;
      display: block;
      width: 35.83%;
      height: 100%;
      background: url('../../resources/img/web/main/book_v3_pattern02.png') no-repeat 100% 100%;
      background-size: 100%;
    }
    .main-contents{
      flex-direction: column-reverse;
      z-index: 1;
    }
    .main-text{
      margin-bottom: 3rem;
      text-align: center;

      h2{
        color: #111;
        font-size: 3.2rem;
      
        strong{
          display: block;
          color: #111;
          font-size: 5.6rem;
        }
      }
      p{
        margin-top: 3rem;
        color: #666;
      }
    } 
    .main-visual{
      position: relative;
      width: 78rem;
      height: 63%; 

      &-item{
        width: 100%;
        height: 100%;
        background: url('../../resources/img/web/main/book_v3_img01.png') no-repeat 50% 100%;
        background-size: contain;
        
        &::before{
          content: '';
          display: inline-block;
          position: absolute;
          bottom: 0;
          left: 0;
          z-index: -1;
          width: 100%;
          height: 71.16%;
          border-radius: 100rem;
          background-color: #09A0FF;
        }
      }
    }
  }
  #section04{
    position: relative;
    background-color:#4EC256;

    &::before{
      content: '';
      position: absolute;
      left: 0;
      top: 5.6rem;
      display: block;
      width: 200%;
      height: 474px;
      background: url('../../resources/img/web/main/book_v4_pattern.png') no-repeat;
    }

    .main-contents{
      display: block;
      width: 100%;
      height: 100%;
    }
    .main-text{
      float: left;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      height: 100%;
      margin: 0 8.75rem 0 10rem;

      h2{
        color: #fff;

        strong{
          color: #fff;
          font-size: 5.6rem;
        }
      }
      p{
        color: #fff;
        
        &.only-mob{
          display: none;
        }
      }
    }
    .main-visual{
      display: flex;
      align-items: center;
      height: 100%;
    }
    .swiper{
      &-container{
        padding: 0 0 11rem 5rem;
        margin-top: 10rem;

        &::before{
          content: '';
          position: absolute;
          left: 0;
          top: -5%;
          z-index: 2;
          display: block;
          width: 3rem;
          height: 100%;
          box-shadow: 11px 14px 20px 10px rgba(78, 194, 86, 1);
          background: rgb(78, 194, 86);
        }
      }
      &-slide{
        position: relative;
        max-width: 44.8rem;
        height: 61.7rem;
        background: #fff;
        border-radius: 7rem;
        box-shadow: 50px 50px 62px 0px #348F3A;
      }
      &-card{
        position: relative;
        height: 100%;
        padding: 6.4rem;
        text-align: center;
        
        &::before{
          content: '';
          position: absolute;
          bottom: 2.5rem;
          left: 50%;
          transform: translateX(-50%);
          width: 29rem;
          height: 29rem;
          //background-size: contain;
          background-repeat: no-repeat;
        }
        strong{
          @include v4CardTitle;
          word-break: keep-all;

          span{
            position: relative;
            @include v4CardTitle;
  
            &::before{
              content: '';
              position: absolute;
              left: -6px;
              top: -2px;
              z-index: -1;
              display: block;
              width: 2.4rem;
              height: 3.2rem;
              transform: skewX(178deg) skewY(347deg);
              border-radius: .3rem;
            }
          }
        }
        p{
          margin-top: 2.2rem;
          color: #111;
          font-family: 'pretendard';
          font-size: 2.2rem;
          letter-spacing: -.025rem;
          line-height: 1.4;
          word-break: keep-all;
        }
        &.n1{
          span::before{
            background-color: #FFB8C2;
          }
          &::before{
            background-image: url('../../resources/img/web/main/book_v4_apple.png');
          }
        }
        &.n2{
          span::before{
            background-color: #A8DDFF;
          }
          &::before{
            background-image: url('../../resources/img/web/main/book_v4_glasses.png');
          }
        }
        &.n3{
          span::before{
            background-color: #ABEE76;
          }
          &::before{
            background-image: url('../../resources/img/web/main/book_v4_light.png');
          }
        }
        &.n4{
          span::before{
            background-color: #FFD914;
          }
          &::before{
            background-image: url('../../resources/img/web/main/book_v4_puzzle.png');
          }
        }
        &.n5{
          span::before{
            background-color: #CBB3FF;
          }
          &::before{
            background-image: url('../../resources/img/web/main/book_v4_game.png');
          }
        }
        &.n6{
          span::before{
            background-color: #FFAD7F;
          }
          &::before{
            bottom: 0;
            background-image: url('../../resources/img/web/main/book_v4_graph.png');
          }
        }
      }
      &-controller{
        position: absolute;
        display: flex;
        justify-content: space-between;
        left: 10rem;
        bottom: 20%;
        width: 10.8rem;
        height: 2.8rem;
      }
      &-button-prev, &-button-next{
        position: static;
        width: 2.8rem;
        height: 2.8rem;
        background: url($swiper_arrow_white) 50% no-repeat;
        background-size: contain;
      }
      &-button-prev{
        transform: rotate(0);
      }
      &-button-next{
        transform: rotate(180deg);
      }
      &-button-stop{
        width: 2.8rem;
        height: 2.8rem;
        background: url($swiper_stop) no-repeat;
        background-size: contain;
      }
      &-button-play{
        display: none;
        width: 2.8rem;
        height: 2.8rem;
        border-radius: 50%;
        background: url($swiper_play) no-repeat;
        background-size: contain;
      }
    }
  }
  #section05{
    display: block;
    text-align: center;
    
    .main-contents{
      display: block;
    }
    .main-text{
      h2 {
        font-size: 3.2rem;

        strong{
          font-size: 5.6rem;
        }
      }
      p.only-mob{
        display: none;
      }
    }
    .main-visual{
      position: relative;
      width: 100%;
      margin-top: 10rem;

      .swiper{
        &-pagination{
          position: absolute;
          top: -9rem;
        }
        &-slide{
          width: calc(100vw - 25%);
          display: flex;
          justify-content: center;
          margin-top: 1rem;
          margin-right: 2rem;

          &:nth-child(2n){
            .swiper-img-box{
              img{
                border-radius: 50%;
              }
            }
          }
        }
        &-img-box{
          width: 39.3rem;
          height: auto;
          margin: 0 2rem;

          img{
            border-radius: 5rem;
          }
        }
        &-pagination-wrap{
          position: absolute;
          top: -7rem;
          font-size: 0;
          width: 100%;
          height: 4.5rem;
          margin: 0;
        }
        &-pagination-bullet{
          width: auto;
          height: 4.1rem;
          padding: .8rem 2rem;
          margin: .3rem;
          border: 1px solid #999;
          border-radius: 10rem;
          background: none;

          color: #999;
          font-family: 'binggrae';
          font-size: 1.8rem;
          letter-spacing: -.06rem;

          &.active{
            color: #fff;
            font-weight: 700;
            border-color: #09A0FF;
            background-color: #09A0FF;
          }
        }
      }
    }
    .main-visual + .main-text{
      margin-top: 4.5rem;

      strong, span{
        font-size: 2rem;
        
        span::before{
          bottom: -.25px;
        }
      }
      p{
        margin-top: 1.8rem;
        font-size: 1.6rem;
        font-family: 'binggrae';
        line-height: 1.4;
      }
    }
  }
  #section06{
    background-color:#ED4043;
  
    .main-contents{
      flex-direction: column;
    }
    .main-text{
      //margin-top: 10rem;
      margin-bottom: 5%;
      text-align: center;

      h2{
        color: #fff;
        font-size: 3.2rem;
      
        strong{
          display: block;
          color: #fff;
          font-size: 5.6rem;
        }
      }
    }
    .main-visual{
      position: relative;
      height: 30rem;
      margin-top: 7rem;
    }
    .postscript.woman{
      &::before{
        background: url('../../resources/img/web/main/book_v6_woman.png') 50% no-repeat;
      }
    }
    .postscript{
      position: relative;
      // width: 45.6rem;
      // height: 37.6rem;
      width: 25vw;
      height: 43vh;
      padding: 15rem 4rem 4rem;
      text-align: center;
      background-color: #fff;
      transition: width 400ms;
      -webkit-user-drag: none;
      border-radius: 4.68rem;

      &::before{
        content: '';
        position: absolute;
        top: 6.4rem; //-14rem
        left: 50%;
        width: 17.5rem;  //176px;
        height: 17.5rem;  //175px;
        background: url('../../resources/img/web/main/book_v6_man.png') 50% no-repeat;
        transform: translateX(-50%);
      }
      p:not(.user-info){
        overflow: hidden;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        margin: 2.5rem 0;
        color: #111;
        font-size: 1.375rem;
        font-weight: 700;
        line-height: 1.9rem;
        text-overflow: ellipsis;
        word-break: keep-all;
      }
      .user-info{
        color: #666;
        font-family: 'binggrae';
        font-size: 1.8rem;

        span{
          font-family: inherit;
          font-size: inherit;
        }
      }
    }
    /* new */
    .carousel-wrap{
      position: relative;
      width: 100%;
      height: 75%;

      #carousel{
        position: relative;
        height: 30rem;
        margin-top: 5rem;
        z-index: 1;
  
        div{
          position: absolute;
          transition: transform 400ms, left 400ms, opacity 400ms, z-index 0s;
          opacity: 1;
        } 
        .hideLeft{
          left:-25%;
          opacity: 0;
          transform: translateY(50%) translateX(-50%);
  
          .postscript{
            width: 17rem;
          }
        }
        .hideRight{
          left: 100%;
          opacity: 0;
          transform: translateY(50%) translateX(-50%);
  
          .postscript{
            width: 17rem;
          }
        }
        .prev{
          z-index: 5;
          left: 11.9%;
          transform: translateY(50px) translateX(-50%);
          opacity: 0.7;
          filter: blur(3px);
  
          .postscript{
            transform: scale(.95);
            padding: 19rem 10rem 0;
  
            &::before{
              top: 4.8rem;
              width: 13.8rem;
              height: 13.8rem;
              transform: scale(1) translateX(-50%);
              background-size: contain;
            }
            .user-info{
              font-size: .8rem;
            }
            p:not(.user-info){
              margin: 3rem 0;
              font-size: 1.6rem;
              line-height: 1.4;
            }
          }
        }
        .prevLeftSecond{
          z-index: 4;
          left: -7.9%;
          transform: translateY(50px) translateX(-50%);
          opacity: 0.4;
          filter: blur(3px);
  
          .postscript{
            transform: scale(.7);
            padding: 18rem 8rem 0;
  
            &::before{
              top: 5.2rem;
              transform: scale(1) translateX(-50%);
              width: 9.8rem;
              height: 9.8rem;
              background-size: contain;
            }
            .user-info{
              font-size: 1rem;
            }
            p:not(.user-info){
              margin: 2.3rem 0;
              font-size: 1.2rem;
              line-height: 1.4;
            }
          }
        }
        .selected{
          z-index: 10;
          left: 34%;
          transform: translateY(0px) translateX(-50%);
  
          .postscript{
            width: 32vw;
            height: 61vh;
            // width: 60rem;
            // height: 50rem;
            padding: 25rem 6.5rem 6.4rem;
  
            .user-info{
              font-size: 1.8rem;
            }
            p:not(.user-info){
              margin: 2.5rem 0;
              font-size: 2.2rem;
              line-height: 1.4;
            }
          }
        }
        .next{
          z-index: 5;
          left: 63.6%;
          transform: translateY(50px) translateX(-50%);
          opacity: 0.7;
          filter: blur(3px);
          
          .postscript{
            transform: scale(.95);
            padding: 19rem 10rem 0;
  
            &::before{
              top: 4.8rem;
              width: 13.8rem;
              height: 13.8rem;
              transform: scale(1) translateX(-50%);
              background-size: contain;
            }
            .user-info{
              font-size: .8rem;
            }
            p:not(.user-info){
              margin: 3rem 0;
              font-size: 1.6rem;
              line-height: 1.4;
            }
          }
        }
        .nextRightSecond{
          z-index: 4;
          left: 83.2%;
          transform: translateY(50px) translateX(-50%);
          opacity: 0.4;
          filter: blur(3px);
  
          .postscript{
            transform: scale(.7);
            padding: 18rem 10rem 0;
  
            &::before{
              top: 5.2rem;
              transform: scale(1) translateX(-50%);
              width: 9.8rem;
              height: 9.8rem;
              background-size: contain;
            }
            .user-info{
              font-size: 1rem;
            }
            p:not(.user-info){
              margin: 2.3rem 0;
              font-size: 1.2rem;
              line-height: 1.4;
            }
          }
        }
      }
      .carousel-buttons{
        position: absolute;
        left: 50%;
        top: 50%;
        z-index: 1;
        width: 42%;
        height: 60px;
        margin-top: 2rem;
        transform: translateX(-50%) translateY(-50%);
  
        &-prev, &-next{
          position: absolute;
          width: 5.6rem;
          height: 5.6rem;
          top: 0;
          background: url($swiper_arrow_black) 50% no-repeat;
          background-size: contain;
        }
        &-prev{
          transform: rotate(180deg);
        }
        &-next{
          transform: rotate(0);
          right: 0;
        }
      }
    }
  }
  #section07{
    background-color: #FFCE1F;

    &::before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      width: 100%;
      height: 100%;
      background: url('../../resources/img/web/main/book_v7_pattern.png') no-repeat;
