        position: relative;
        width: 68vw;
        height: 36vh;
        padding: 15rem 4rem 4rem;
        text-align: center;
        background-color: #fff;
        transition: width 400ms;
        -webkit-user-drag: none;
        border-radius: 4.68rem;
  
        &::before{
          content: '';
          position: absolute;
          top: 6.4rem; //-14rem
          left: 50%;
          width: 12rem;  //176px;
          height: 12rem;  //175px;
