                font-size: 1.2rem;
              }
              p:not(.user-info){
                margin: 3.5rem 0;
                font-size: 1.4rem;
                line-height: 1.4;
              }
            }
          }
          .selected{
            width: calc(100% - 4.8rem);
            z-index: 10;
            left: 50%;
            transform: translateY(0px) translateX(-50%);
    
