                font-size: 1.2rem;
              }
              p:not(.user-info){
                margin: 3rem 0;
                font-size: 1.4rem;
              }
            }
          }
          .next{
            z-index: 5;
            left: 60%;
            transform: translateY(50px) translateX(-50%);
            opacity: 0.7;
            filter: blur(3px);
            
            .postscript{
              transform: scale(.95);
              padding: 12rem 2rem 0;
    
