                top: 3.5rem;
                width: 9.6rem;
                height: 9.6rem;
                transform: scale(1) translateX(-50%);
                background-size: contain;
              }
              .user-info{
                font-size: .8rem;
              }
              p:not(.user-info){
                margin: 3.5rem 0;
                font-size: 1.6rem;
                line-height: 1.4;
              }
            }
          }
        }
        .carousel-buttons{
          width: calc(100% - 1rem);
          height: 5rem;
          margin-top: 3rem;

          &-next, &-prev{
            width: 5rem;
            height: 5rem;
          }
