@charset "utf-8";

.txt-blue{color: #09A0FF !important;}
.txt-l{text-align: left !important;}
.stxt{
  font-family: 'pretendard';
  font-size: 2.4rem;
  font-weight: 700;
}
.highlight{
  position: relative;
  display: inline-block;
  color: #111;
  font-family: 'binggrae';
  font-size: 3.2rem;
  font-weight: 700;

  span{
    color: #111;
    font-family: 'binggrae';
    font-size: 3.2rem;
    font-weight: 700;
    letter-spacing: -.064rem;

    
    &::before{
      content: '';
      position: absolute;
      bottom: 5px;
      left: -0.2rem;  
      z-index: -1;
      width: 100%;
      height: 1.8rem;
      background-color: #C5E0FF;
    }
  }
}

/* 로그인, 회원가입 등 */
.info-txt{
  margin: 0.8rem 0 1.6rem;
  text-align: right;
  font-family: "pretendard";
  font-size: 1.2rem; 
  font-weight: 400;
  letter-spacing: -.024rem;

  &.error{
    color:#E51F33;
  }
}

/* 로그인 폼 */
.section-m{
  .wrap{
    position: relative;
    //z-index: 1;
    //margin-top: 3rem;

    &.nogap{
      position: relative;
      margin-top: 0;

      &::before{
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        display: block;
        width: 100%;
        height: 100%;
        background: url('../../resources/img/web/square_bg.png') 50% no-repeat;
        background-size: cover;
      }  
      .wrap{
        margin: 0;
      }
      .container{
        margin-top: 0;
        min-height: calc(100vh - 9.6rem);
      }
      .section-m-box{
        .btn.primary{
          width: 100%;
        }
      }
    }
    &.nomenu{
      position: relative;
      margin-top: 0;

      .container{
        min-height: 100vh;
        margin-top: 0;
      }

      &::before{
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        display: block;
        width: 100%;
        height: 100%;
        background: url('../../resources/img/web/square_bg.png') 50% no-repeat;
        background-size: cover;
      }  
    }
  }
  .container{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
  }
  &-logo{
    display: block;
    width: 177px;
    height: 18px;
    background: url($logo_s) 50% 50% no-repeat;
  }
  &-area{
    width:46rem;
    margin: 4.2rem auto 10rem;
    background-color: #fff;

    &.line{
      margin: 1.8rem auto 0;
      padding: 3.2rem 3rem;
      border: 1px solid #C6C6C6;
      border-radius: 3rem;

      .form-s-content{
        padding-top: 3.2rem;
      }
    }
  }
  &-box{
    h2{
      // padding-bottom: 3.2rem;
      color: #111;
      font-family: 'binggrae';
      font-size: 2.4rem;
      font-weight: 700;
      letter-spacing: -.03rem;
      text-align: center;
    }
    .btn-wrap{
      .btn.primary{
        font-size: 1.8rem;
        font-weight: 700;
      }
    }
  }
  &-content{
    padding-top: 2.3rem;
  }
  &-item{
    display: block;
    height: 4.8rem;
    border: 1px solid #C6C6C6;
    background-color: #fff;

    &:first-of-type{
      border-top-left-radius: 1rem;
      border-top-right-radius: 1rem;
    }
    &:last-of-type{
      border-top: 0;
      border-bottom-left-radius: 1rem;
      border-bottom-right-radius: 1rem;
    }

    input{
      width: 100%;
      height: 4.8rem;
      padding: 1.4rem 1.6rem; 
      color: #111;
      font-family: 'pretendard';
      font-size: 1.4rem;
      font-weight: 700;
      line-height: 1.2rem;

      &::placeholder{
        color:#111;
        font-family: 'pretendard';
      }
    }
  }
  &-opt{
    margin-top: 1.2rem; 
    text-align: right;
    line-height: 1;

    span, .btn-underline{
      font-family: 'pretendard';
      font-size: 1.4rem;
      font-weight: 700;
    }
    span{
      color:#888;
    }
    .btn-underline{
      margin-left: 1rem;
      color:#000;
    }
  }
  &-find{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1.6rem;

    ul{
      display: flex;

      li:first-child{
        position: relative;
        padding-right: 1.4rem;

        &::after{ 
          content:'';
          display: block;
          position: absolute;
          right: 6px;
          top: 50%;
          transform: translateY(-50%);
          width: 1px;
          height: 12px;
          background-color:#D9D9D9;
        }
      }
    }
    label > span, a{
      color: #888;
      font-family: 'pretendard';
      font-weight: 700;
      font-size: 1.4rem;
    }
  }
}

.bx-line{
  overflow: hidden;
  height: 23vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  border: 1px solid #C6C6C6;
  border-radius: 1rem;

  &.scroll{
    .inner{
      width: 100%;
      overflow-y: overlay;
      padding: 2rem;
      text-align: left;
      scrollbar-width: thin;
      scrollbar-color: black white;

      &::-webkit-scrollbar{
        width: 12px;
      } 
      &::-webkit-scrollbar-thumb{
        background: #D9D9D9;
        border: 2px solid #fff;
        border-radius: 12px;
      }
      ::-webkit-scrollbar-thumb:hover {
        background-color:#a0a0a5;
      }
      ::-webkit-scrollbar-track{
        background-color: rgba(255, 255, 255, 0.8);
      }
    }
  }
  &.result{
    height: 20vh;

    .inner{
      text-align: center;
      div{
        margin-bottom: 1rem;
        &:last-child{
          margin-bottom: 0;
        }
      }
      strong{
        color: #111;
        font-family: 'pretendard';
        font-size: 2rem;
        line-height: 2.8rem;
        letter-spacing: -.04rem;
  
        span{
          font-size: inherit;
        }
      }
      p {
        margin-top: 1rem;
        color: #000;
        font-family: 'pretendard';
        font-size: 1.4rem;
        letter-spacing: -.028rem;
        word-break: keep-all;
  
        span{
          font-family: inherit;
          font-size: 1.4rem;
          font-weight: 700;
        }
      }
    }
  }
}

.btm-area{
  padding-top: 2rem;
  margin-top: 3.2rem;
  border-top: 1px solid #C6C6C6;
  text-align: center;

  p{
    color: #666;
    font-family: 'pretendard';
    font-size: 1.4rem;
    letter-spacing: -0.028rem;
    word-break: keep-all;
    line-height: 1.3;
  }
}

/* 가입완료 */
.wrap{
  &.welcome{
    .container{
      margin-top: 0;
    }
    .complete{
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      height: calc(100vh - 9.6rem);
      margin: 0 auto;
      text-align: center;
      
      &-header{
        &::after{
          content:'';
          display: block;
          width: auto;
          height: 17.3rem;
          margin-top: 5.2rem;
          background:url($join_complete_img) 50% no-repeat;
          background-size: contain; 
        }
      }
      &-tit{
        color: #111;
        font-family: 'binggrae';
        font-size: 4.2rem;
        font-weight: 700;
        letter-spacing: -.084rem;
        line-height: 5.8rem;
    
        & + p{
          margin-top: 2.4rem;
          font-family: 'binggrae';
          font-size: 2rem;
          line-height: 2.8rem;
          letter-spacing: -.04rem;
        }
      }
      .btn-wrap{
        margin-top: 5.15rem;
    
        .btn.round{
          width: 24rem;
          height: 6.7rem;
          font-size: 2rem;
        }
      }
    }
  }
}

.tbl{
  &-col{
    thead{
      th{
        border-top: 1px solid #111;
        border-bottom: 1px solid #111;
        padding: 3.2rem 0;
        font-weight: 700;
      }
    }
    td, th{
      padding: 3.2rem 2.5rem 3.2rem;
      color: #111;
      font-family: 'pretendard';
      font-size: 1.6rem;
      letter-spacing: -0.032rem;
      text-align: center;

      a{
        overflow: hidden;
        display: block;
        max-width: 100%;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-family: inherit;
        font-weight: 700;

        &:hover, &:focus{
          color: #089FFF; 
        }
      }
    }
    tbody{
      td{
        border-bottom: 1px solid #E6E6E6;

        &:last-child{
          padding-left: 0;
          padding-right: 0;
        }
      }
    }
    .tbl-cell {
      @for $i from 1 through 10 {
          &-1of#{$i} {
            // width: percentage(1/$i);
            width: percentage(1/$i);
          }
      }
      &-3of4 { width: 75%; }
    }
  }
}

.paging{
  margin-top: 4rem;

  ul{
    display: flex;
    justify-content: center;

    li{margin: 0.2rem;}
  }
  a{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.6rem;
    height: 2.6rem;
    color: #fff;
    font-family: 'pretendard';
    font-size: 1.6rem;
    line-height: 1;
    background-color: #C6C6C6;

    &:hover, &:active, &:focus{
      background-color: #111;
    }
    &.current{
      background-color: #111;
    }
  }
  button{
    position: relative;
    width: 2.6rem;
    height: 2.6rem;

    &.prev::before, &.next::before{
      content:'';
      position: absolute;
      left: 50%;
      top: 50%;
      display: inline-block;
      width: 1.2rem;
      height: 1.2rem;
      background: url('../../resources/img/web/ico_arrow_prev.png') 50% 50% no-repeat;
      background-size: contain; 
      transform: translate(-50%, -50%);
    }
    &.next::before{
      transform: translate(-50%, -50%) rotate(180deg);
    }
  }
}

.board-detail{
  &-head{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 4rem;
    border-top: 1px solid #111;
    border-bottom: 1px solid #E6E6E6;

    p{
      font-size: 1.6rem;
      font-family: 'pretendard';
    }
  }
  &-tit{
    width: calc(100% - 9rem);
    color: #111;
    font-family: 'pretendard';
    font-size: 2.3rem;
    font-weight: 700;
    line-height: 1;
  }
  &-opt{
    padding: 1.5rem 0 1.5rem 6rem;
    border-bottom: 1px solid #eee;
    
    a{
      display: block;
      position: relative;
      margin-right:2rem;
      color: #089FFF;
      font-family: 'pretendard';
      font-size: 1.4rem;
      text-decoration: underline;
    }
  }
  &-cont{
    padding: 4.5rem 4rem;
    
    .content{
      font-family: 'pretendard';
      line-height: 1.6;
    }
  }
}

.board-preview{
  &-list{
    margin-top: 3.3.2rem;
    margin-bottom: 3.5rem; 
    border-bottom: 1px solid #E6E6E6;
    
    li{
      display: flex;
      justify-content: space-between;
      padding: 3.2rem 4.3rem;
      border-top: 1px solid #E6E6E6; 
      
      .tit{    
        flex: 1;
        padding-left: 4.2rem;
        text-align: left;
        color: #888;
        font-family: "pretendard"; 
        font-size: 1.6rem;
        font-weight: 700;

        a{
          color: #111;
          font-family: inherit;  
          font-size: 1.6rem;
          font-weight: 700;
        }
      }
      span, .date{
        color: #111;
        font-size: 1.6rem;
        font-family: 'pretendard';
      }

    }

  }
  &-wrap{
    margin-bottom: 10rem; 

    .btn-wrap{
      margin-top: 0;
      margin-bottom: 5rem;
    }
  }
}

/* accodian */
.ui-acco{
  border-top: 1px solid #111; 
  border-bottom:1px solid #111;

  &-wrap{
    border-top: 1px solid #E6E6E6;

    &.active{
      .ui-acco-btn{
        position: relative;

        span{
          word-break: break-all;
          color:#089FFF;
          font-weight: 700;
        }
        &::after{
          transform: translateY(-50%) rotate(180deg);
        }
      }
    }
  }
  &-tit{
    position: relative; 
    width: 100%; 
    height: 100%; 
    min-height: 3.1rem; 
    display: flex; 
    justify-content: space-between; 
    align-items: center;

    > div{
      z-index: 1;
      padding: 0 1rem;
      font-size: 1.4rem;
    }

    &::before{
      content: 'Q';
      color: #089FFF;
      font-family: 'binggrae';
      font-size: 1.6rem;
      font-weight: 700;
      padding: 0 5.7rem;
    }
  }
  &-btn{
    position: relative;
    display: block;
    width: 100%;
    padding: 3.2rem 6rem 3.2rem 0;
    line-height: 1.4;
    text-align: left;

    &::after{
      content: '';
      z-index: -1;
      position: absolute;
      right: 2.5rem;
      top: 50%;
      display: inline-block;
      width: 2.6rem;
      height: 1.5rem;
      background: url('../../resources/img/web/ico_arrow_acco.png') 50% no-repeat;
      background-size: contain;
      transform: translateY(-50%);
    }
    span{
      color: #111;
      font-family: 'pretendard';
      font-size: 1.6rem;
      font-weight: 700;
      line-height: 1.2;
      word-break: break-all;
    }
  }
  &-panel{
    position: relative;
    display: none;
    border-top: 1px solid #E6E6E6;
    background:#fff;

    &::before{
      content: 'A';
      position: absolute;
      top: 5.25rem;
      color: #ED4043;
      font-family: 'binggrae';
      font-size: 1.6rem;
      font-weight: 700;
      padding: 0 5.7rem;
    }
  }
  &-cont{
    padding: 5.2rem 12.5rem;
    line-height: 1.4;
    word-break: keep-all;

    p{
      word-break: break-all;
      font-family: 'pretendard';
      line-height: 1.6;
      
      div {
        word-break: break-all;
      }
    }
  }
}

.tit-box{
