.swiper{
  &-content{
    margin: 0 auto;
  }
  &-txt{
    margin-top: 8.75rem;
    color: #111;
    text-align: center;

    strong{
      color: #111;
      font-family: 'binggrae';
      font-size: 3.2rem;
      text-align: center;
      font-weight: 700;
      letter-spacing: -0.04rem;

      span{
        display: inline-block;
        margin-right: 2rem;
        color: #089FFF;
        font-size: 3.2rem;
        font-weight: 700;
        vertical-align: middle;
      }
    }
    p{
      margin-top: 2.5rem;
      font-size: 2.4rem;
      letter-spacing: -.048rem;
      line-height: 1.4;
      word-break: keep-all;
    }
  }
  &-img-box{
    width: fit-content;
    height: 69rem;
    margin: 0 auto;
  }
  &-slide{
    border-radius: 1.56rem;
  }
  &-button-wrap{
    z-index: 1;
    position: absolute;
    top: 34.5rem;  //13vw
    left: 50%;
    width: 100%;
    height: 4.4rem;
    transform: translate(-50%, -50%);
  }
  &-button-prev, &-button-next{
    position: absolute;
    top: 50%;
    display: inline-block;
    width: 2.8rem;
    height: 4.4rem;  
    background: url($swiper_arrow) 50% no-repeat;
    background-size: contain;
  }
  &-button-prev{
    left: 0;
    transform: translateY(-50%) rotate(180deg);
    &:disabled{
      background-color: transparent;
    }
  }
  &-button-next{
    right: 0;
    transform: translateY(-50%);
    &:disabled{
      background-color: transparent;
    }
  }
  &-pagination{
    position: relative;
    height: auto;
    bottom: 19rem;
  }
  &-pagination-bullet{
    position: relative;
    display: inline-block;
    width: 1.4rem;
    height: 1.6rem;
    margin-left: 1.1rem;
    vertical-align: top;
    cursor: pointer;
    background: url($swiper_bullet_off) 50% no-repeat;
    background-size: contain;
    border-radius: 0;

    &-active{
      background-image: url($swiper_bullet_on);
    }
  }
  &-head{
    margin: 11rem auto 4rem;
    text-align: center;

    strong{
      font-family: 'binggrae';
      font-size: 3rem;
      font-weight: 700;
    }
  }
}

.tablet-box{
  position: relative;
  margin: 0 auto;
  max-width: 1060px;

  &.num-type{
    padding-top: 12rem;
    margin-bottom: 10rem;

    .swiper-txt{
      margin-top: 4rem;
    }
    .swiper-img-box{
      width: fit-content;
      height: 68rem;
    }
    .swiper-pagination{
      position: absolute;
      bottom: auto;
      top: 0;

      &-bullet{
        width: 8.2rem;
        height: 5.9rem;
        padding: 1.6rem 2.8rem;
        margin-left: 2.8rem;
        line-height: 1.4;
        background: #fff;
        border-radius: 3rem;

        span{
          color: #111;
          font-family: 'binggrae';
          font-size: 2rem;
          font-weight: 700;
        }
      }
    }
  }
  &.txt-type{
    position: relative;
    max-width: 100%; 
    margin-bottom: 8rem;

    &::after{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      width: 100%;
      height: 100%;
      background: url('../../resources/img/web/digital_srv_bg.png') 0 5rem no-repeat;
      background-size: contain;
    }
    .swiper-txt{
      margin: 11rem 0 5rem;
      
      p{
        margin-top: 0;
        font-weight: 700;
        font-family: 'pretendard';
      }
    }
    .swiper-img-box{
      width: 90rem;
      height: 71rem;
    }
    .swiper-pagination{
      position: absolute;
      bottom: auto;
      top: -3rem;

      &-bullet{
        width: auto;
        height: 5.9rem;
        padding: 1.6rem 2.8rem;
        margin-left: 1.25rem;
        background: #fff;
        border: 1px solid #111;
        border-radius: 3rem;

        span{
          color: #111;
          font-family: 'binggrae';
          font-size: 2rem;
          font-weight: 700;
        }
        &-active{
          background-color: #09A0FF;
          border-color: #09A0FF;
  
          span{
            color: #fff;
          }
        }
      }
    }
    .swiper-button-wrap{
      top: 50rem;
    }
  }
  .btn-wrap{
    width: fit-content;
    margin: 5rem auto 10rem;
  }
}

