.standing-order{
  margin-bottom: 10rem; 
  
  &-step{
    counter-reset: num;
    display: flex;
    justify-content: space-between;
    padding: 1.5rem 0 10rem;
    border-bottom: 1px solid #999;
    
    > li{
      position: relative;
      width: calc(33.3% - 5.2rem);
      height: 35.8rem;
      text-align: center;
      border-radius: 3rem;
      background-color: #fff;
      box-shadow: 0.25rem 0.25rem 2.5rem 0 rgba(0, 0, 0, 0.14);

      &::before{
        content: '0'counter(num);
        counter-increment: num;
        position: absolute;
        left: 50%;
        top: -1.5rem;
        display: block;
        width: 9rem;
        height: 4.8rem;
        padding: 0.7rem 1.8rem;
        line-height: 1.6;
        color: #fff;
        font-family: 'binggrae';
        font-size: 2.2rem;
        font-weight: 700;
        text-align: center;
        background-color: #111;
        border-radius: 10rem;
        transform: translateX(-50%);
      }
      &::after{
        content: '';
        display: inline-block;
        position: absolute;
        right: -5.5rem;
        top: 50%;
        width: 2.8rem;
        height: 4.4rem;
        background:url($swiper_arrow) 50% no-repeat;
        background-size: contain;
        transform: translateY(-50%);
      }
      &:last-child::after{
        display: none;
      }
      &:nth-child(1){
        .cont{
          &::before{
          top: 7.5rem;
          background: url('../../resources/img/web/standing_order_img01.png') 50% no-repeat;
          background-size: contain;
          }
        }
      }
      &:nth-child(2){
        .cont{
          &::before{
          top: 7rem;
          background: url('../../resources/img/web/standing_order_img02.png') 50% no-repeat;
          background-size: contain;
          }
        }
      }
      &:nth-child(3){
        .cont{
          &::before{
          top: 6.5rem;
          background: url('../../resources/img/web/standing_order_img03.png') 50% no-repeat;
          background-size: contain;
          }
        }
      }
    }
    .cont{
      position: relative; 
      display: inline-block;
      padding-top: 23.6rem;
      color: #111;
      font-family: 'binggrae';
      font-size: 2.6rem;
      font-weight: 700;
    
      &::before{
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        display: block;
        width: 120px;
        height: 120px;
        transform: translateX(-50%);
      }
    }
  }
  &-list{
    display: flex;
    justify-content: space-between;
    margin-top: 13.2rem;
  }
  &-item{
    position: relative;
    width: calc(25% - 1.1rem);

    input:checked + label{
      background: url($radio_ico_on) right 1.8rem top 1.8rem no-repeat;
      border-color: #111;
      box-shadow: 8px 8px 20px 0px rgba(52, 52, 52, 0.25);
    }
    label{
      position: relative;
      display: block;
      padding: 5.7rem 2.1rem 4.2rem;
      background: #FFF;
      border: 1px solid #C6C6C6;
      border-radius: 1.875rem;
      background: url($radio_ico_off) right 1.8rem top 1.8rem no-repeat;
      background-size: 2.8rem;
    }
    .tip{
      position: absolute;
      left: 2rem;
      top: -7rem;
      display: inline-block;
      width: 34.6rem;
      height: 4rem;
      padding: 1.1rem 0;
      text-align: center;
      color: #111;
      font-family: 'binggrae';
      font-size: 1.8rem;
      font-weight: 700;
      background-color: #FFD231;
      border-radius: 3rem;

      &::before{
        content: '';
        position: absolute;
        bottom: -1rem;
        left: 0.5rem;
        border-top: 1.25rem solid #FFD231;
        border-right: 1.25rem solid transparent;
        border-bottom: 1.25rem solid transparent;
        border-left: 1.25rem solid transparent;
        transform: rotate(135deg);
      }
    }
  }
  &-head{
    display: block;
    padding-bottom: 5rem;
    color: #111;
    font-family: 'binggrae';
    font-size: 2rem;
    text-align: center;
    letter-spacing: -.04rem;

    strong{
      display: block;
      margin-top: .4rem;
      font-family: 'binggrae';
      font-size: 3.4rem;
      font-weight: 700;
    }
  }
  &-cont{
    padding: 3.7rem 1rem 0;
    background: url('../../resources/img/web/line_dash_bg.png') .8rem 0 repeat-x;
    background-size: 16px 2px;
    letter-spacing: -.03rem;

    div{
      display: flex;
      justify-content: space-between;
      align-items: center;
      
      &:not(:first-child){
        margin-top: 1rem;
      }
    }
    dt, dd{font-family: 'binggrae';}
    dt{font-size: 2rem;}
    dd{
      font-size: 2.4rem;
      font-weight: 700;  
    }
    span{
      font-size: 2.4rem;
      font-weight: 700;
    }
  }
}

