  .mob .menu {
    height: 100%;
    margin-top: 5rem;
  }
  .mob .menu__item {
    margin-top: 3rem;
    padding: 0 2.4rem;
  }
  .mob .menu__anchor {
    font-size: 2.2rem;
  }
  .mob .menu__sub-item {
    margin-top: 1rem;
  }
  .mob .menu__sub-item:first-child {
    margin-top: 0;
  }
  .mob .menu__sub-list {
    margin-top: 1.7rem;
    padding-top: 2rem;
    border-top: 1px solid #000;
  }
  .mob .menu__sub-anchor {
    font-size: 1.5rem;
    line-height: 2;
  }
  .mob .menu__sub-title {
    margin-top: 1.8rem;
    font-size: 1.8rem;
  }
  .mob .menu__user-info {
    display: block;
    padding: 3.4rem 0 3rem 2.4rem;
    margin: 0 2.4rem 5rem 2.4rem;
    border: 1px solid #111;
    border-radius: 2rem;
    box-shadow: 9px 7px 20px 0px rgba(0, 0, 0, 0.3);
  }
  .mob .menu__user-info .btn-login.user {
    position: relative;
    padding: 0 2rem 0 0;
    font-family: "binggrae";
    font-size: 2.2rem;
    font-weight: 700;
    color: #111;
    border: 0;
    background-color: transparent;
    border-radius: 0;
  }
  .mob .menu__user-info .btn-login.user::after {
    content: "";
    position: absolute;
    right: 0;
    top: 50%;
    width: 1rem;
    height: 1.5rem;
    background: url("data:image/svg+xml,%3Csvg width='10' height='15' viewBox='0 0 10 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2.34375 1.84277L8.0006 7.49963L2.34375 13.1565' stroke='%23111111' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") 50% no-repeat;
    background-size: contain;
    transform: translateY(-50%);
  }
  .mob .menu__user-info .btn-login.user span {
    color: #111;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    line-height: 1.3;
  }
  .mob .menu__user-info p {
    margin-bottom: 1rem;
    font-family: "binggrae";
    font-size: 1.2rem;
  }
  .system-message-txt::after {
    width: 100%;
    height: 14rem;
  }
  .system-message-txt strong {
    font-size: 3.2rem;
  }
  .system-message-txt p {
    font-size: 1.4rem;
  }
  .system-message-txt.b2b p {
    margin-top: 24rem;
    font-size: 2.4rem;
  }
  .system-message-txt.b2b::after {
    top: -18rem;
  }
  .sc-sub-area {
    display: none;
  }
  .footer-link__anchor {
    font-family: "Binggrae";
    font-size: 1.4rem;
    letter-spacing: -0.03 0.2rem;
    color: #A8A8A8;
  }
  .footer-link__anchor:hover, .footer-link__anchor:focus {
    color: #A8A8A8;
  }
  .footer-link__item {
    margin-left: 0.7rem;
    padding-left: 0.7rem;
  }
  .footer-link {
    margin-top: 1.5rem;
  }
  .footer-info-list ul > li {
    display: block;
    font-size: 1.4rem;
    padding-left: 0;
    margin: 0.6rem 0 0;
  }
  .footer-info-list ul > li::before {
    display: none;
  }
  .footer-info-list ul > li:first-child {
    padding-left: 0;
    margin: 0.6rem 0 0;
  }
  .footer-info-list ul:nth-child(2) li:last-child {
    margin: 0.6rem 0 0;
  }
  .section .inner.sm {
    max-width: 100%;
  }
  .section .inner.sm .btn-wrap.fx-center {
    margin: 4rem 0;
    justify-content: center;
  }
  .section .inner.sm .btn-wrap.fx-center button {
    min-width: 15rem;
    padding: 0 2rem;
  }
  .section .inner.sm .btn-wrap.fx-center button + button {
    margin-left: 1rem;
  }
  .sitemap {
    height: auto;
    flex-wrap: wrap;
  }
  .sitemap > li {
    width: 50%;
    margin-bottom: 5vh;
  }
  .sitemap > li .tit {
    margin-bottom: 2vh;
  }
  .sitemap > li .menu-list > li {
    margin-top: 2vh;
  }
  .sitemap > li .sub-tit {
    margin-bottom: 2vh;
  }
  .sitemap > li .sub + .sub {
    margin-top: 2vh;
  }
}
@media all and (max-width: 430px) {
  .header .util .btn-menu {
    width: 2.4rem;
    height: 2.4rem;
  }
  .header .util .btn-menu span {
    right: 0;
    left: 0;
  }
  .header .util .btn-menu span:nth-child(3) {
    width: 70%;
  }
  .footer-info-list ul li {
    float: none;
    margin: 0.5rem 0 0 0;
    padding: 0;
  }
  .footer-info-list ul li:first-child {
    margin: 0.5rem 0 0 0;
  }
  .footer-info-list ul li::before {
    display: none;
  }
}
/* popup */
.modal-popup {
  display: none;
  overflow: hidden;
  /* 알럿 */
  /* 로딩 */
  /* 글쓰기 */
}
.modal-popup .terms-wrap {
  margin-top: 0;
}
.modal-popup .terms-wrap > p:first-child {
  margin-top: 0;
}
.modal-popup .terms-tit {
  font-size: 1.6rem;
}
.modal-popup .terms-last-txt {
  margin-bottom: 0;
}
.modal-popup-bg {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 399;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.75;
  display: none;
}
.modal-popup-outer {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 400;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.modal-popup-container {
  position: relative;
  width: 46rem;
  background: #fff;
  padding: 3.2rem 3rem;
  opacity: 0;
  transition: all 0.3s;
  border-radius: 3rem;
  border: 1px solid #C6C6C6;
}
.modal-popup-header {
  position: relative;
  margin-bottom: 3.2rem;
  text-align: center;
}
.modal-popup-title {
  font-family: "binggrae";
  font-size: 2rem;
  font-weight: 700;
