  }
  .inp-list.complex-v1.disabled .lb-tit {
    background-color: #fff;
  }
  .inp-list.complex-v1 .lb-tit {
    height: auto;
    padding: 0;
  }
  .inp-list.complex-v1 .btn {
    min-width: 6.26rem;
  }
  .inp-list.complex-v1 .inp-list-item {
    overflow: visible;
    width: calc(100% - 6.6rem);
  }
  .inp-list.complex-v1 .inp-list-item input {
    width: 100%;
    border-radius: 1rem;
  }
  .inp-list.complex-v1 .inp-list-item input:disabled {
    height: 100%;
    border-radius: 1rem 0 0 1rem;
  }
  .inp-list .lb-tit {
    position: absolute;
    top: -2.5rem;
    height: auto;
    padding: 0;
    line-height: 1;
  }
  .inp-list-email {
    margin-bottom: 3rem;
  }
  .inp-list-email .email-item .inp-list-item {
    width: calc(60% - 1.5rem);
  }
  .inp-list-email .email-item .inp-list-item:first-child {
    margin-right: 1.5rem;
  }
  .inp-list-email .email-item.on .inp-list-item {
    width: calc(33.3% - 0.8rem);
  }
  .inp-list-email .email-item.on .inp-list-item:first-child {
    margin-right: 0.8rem;
  }
  .inp-list-email .email-item.on .inp-list-item::after {
    right: -1.5rem;
  }
  .inp-list + .inp-list {
    margin-top: 4rem;
  }
  .inp-list + .inp-list.inquire {
    margin-top: 1.6rem;
  }
  .file-wrap input {
    width: 100%;
  }
  .file-wrap .file-inp {
    padding: 0;
  }
  .file-wrap .file-inp .lb-tit {
    display: inline-block;
    padding: 1.5rem 0 0 1.5rem;
  }
  .file-wrap .file-inp .file-guide-txt {
    padding-left: 1rem;
  }
  .file-wrap .file-inp .btn {
    position: initial;
    width: 100%;
    height: 4rem;
    margin-top: 1rem;
    border-radius: 0 0 0.625rem 1rem;
  }
