.section-m-area {
  width: 46rem;
  margin: 4.2rem auto 10rem;
  background-color: #fff;
}
.section-m-area.line {
  margin: 1.8rem auto 0;
  padding: 3.2rem 3rem;
  border: 1px solid #C6C6C6;
  border-radius: 3rem;
}
.section-m-area.line .form-s-content {
  padding-top: 3.2rem;
}
.section-m-box h2 {
  color: #111;
  font-family: "binggrae";
  font-size: 2.4rem;
  font-weight: 700;
  letter-spacing: -0.03rem;
  text-align: center;
}
.section-m-box .btn-wrap .btn.primary {
  font-size: 1.8rem;
  font-weight: 700;
}
.section-m-content {
  padding-top: 2.3rem;
}
.section-m-item {
  display: block;
  height: 4.8rem;
  border: 1px solid #C6C6C6;
  background-color: #fff;
}
.section-m-item:first-of-type {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}
.section-m-item:last-of-type {
  border-top: 0;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}
.section-m-item input {
  width: 100%;
  height: 4.8rem;
  padding: 1.4rem 1.6rem;
  color: #111;
  font-family: "pretendard";
  font-size: 1.4rem;
  font-weight: 700;
  line-height: 1.2rem;
}
.section-m-item input::placeholder {
  color: #111;
  font-family: "pretendard";
}
.section-m-opt {
  margin-top: 1.2rem;
  text-align: right;
  line-height: 1;
}
.section-m-opt span, .section-m-opt .btn-underline {
  font-family: "pretendard";
  font-size: 1.4rem;
  font-weight: 700;
}
.section-m-opt span {
  color: #888;
}
.section-m-opt .btn-underline {
  margin-left: 1rem;
  color: #000;
}
.section-m-find {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.6rem;
}
.section-m-find ul {
  display: flex;
}
.section-m-find ul li:first-child {
  position: relative;
  padding-right: 1.4rem;
}
.section-m-find ul li:first-child::after {
  content: "";
  display: block;
  position: absolute;
  right: 6px;
  top: 50%;
  transform: translateY(-50%);
  width: 1px;
  height: 12px;
  background-color: #D9D9D9;
}
.section-m-find label > span, .section-m-find a {
  color: #888;
  font-family: "pretendard";
  font-weight: 700;
  font-size: 1.4rem;
}

.bx-line {
  overflow: hidden;
  height: 23vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  border: 1px solid #C6C6C6;
  border-radius: 1rem;
}
.bx-line.scroll .inner {
  width: 100%;
  overflow-y: overlay;
  padding: 2rem;
  text-align: left;
  scrollbar-width: thin;
  scrollbar-color: black white;
}
.bx-line.scroll .inner::-webkit-scrollbar {
  width: 12px;
}
.bx-line.scroll .inner::-webkit-scrollbar-thumb {
  background: #D9D9D9;
  border: 2px solid #fff;
  border-radius: 12px;
}
.bx-line.scroll .inner ::-webkit-scrollbar-thumb:hover {
  background-color: #a0a0a5;
}
.bx-line.scroll .inner ::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, 0.8);
}
.bx-line.result {
  height: 20vh;
}
.bx-line.result .inner {
  text-align: center;
}
.bx-line.result .inner div {
  margin-bottom: 1rem;
}
.bx-line.result .inner div:last-child {
  margin-bottom: 0;
}
.bx-line.result .inner strong {
  color: #111;
  font-family: "pretendard";
  font-size: 2rem;
  line-height: 2.8rem;
  letter-spacing: -0.04rem;
}
.bx-line.result .inner strong span {
  font-size: inherit;
}
.bx-line.result .inner p {
  margin-top: 1rem;
  color: #000;
  font-family: "pretendard";
  font-size: 1.4rem;
  letter-spacing: -0.028rem;
  word-break: keep-all;
}
.bx-line.result .inner p span {
  font-family: inherit;
  font-size: 1.4rem;
  font-weight: 700;
}

.btm-area {
  padding-top: 2rem;
  margin-top: 3.2rem;
  border-top: 1px solid #C6C6C6;
  text-align: center;
}
.btm-area p {
